import { fetchNoteById } from "@/lib/api";

type Props = {
    params: Promise<{ id: string }>;
};

export default async function NoteDetails({ params }: Props) {
    const { id } = await params;
    const note = await fetchNoteById(id);
    console.log(note);
    return (
        <div>NoteDetails</div>
    )
}